.popup__input {
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin: 0;
    padding: 0;
    text-align: left;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    min-height: 30px;
    width: 100%;
    max-width: 358px;
}

.popup__input::placeholder {
    color: #C4C4C4;
}